@import '../../../scss//mixins';

.MuiGrid-item {
    @media (max-width: 768px) {
        max-width: 100% !important;
    }
}

.detail-history-top {

    display: flex;

    .div-card-post-left {
        width: 80%;

        .MuiImageListItem-img {
            width: 120px !important;
            height: 120px !important;
        }

        .div-card-post-left_info {
            padding: 0;
            margin-left: 12px;
            display: flex;
            flex-direction: column;
            height: 120px;
            justify-content: space-between;
            width: 80%;

            @include breakpoint(mobile) {
                width: 94%;
            }

            @include breakpoint(tablet) {
                width: 90%;
            }

            @include breakpoint(largeTablet) {
                width: 80%;
            }

            @media screen and (max-width: 624px) and (min-width: 500px) {
                width: 80%;
            }

            @media screen and (max-width: 499px) and (min-width: 426px) {
                width: 70%;
            }

            @media screen and (max-width: 1280px) and (min-width: 1240px) {
                width: 90%;
            }

            @media screen and (max-width: 950px) and (min-width: 900px) {
                width: 75%;
            }
        }

        .MuiTypography-h6 {
            @include breakpoint(mobile) {
                font-size: 12px;
                width: 75% !important;
            }

            @include breakpoint(tablet) {
                // width: 90% !important
            }

            @include breakpoint(largeTablet) {
                width: 100% !important;
            }
        }

        .MuiTypography-h1 {
            @include breakpoint(mobile) {
                font-size: 10px;
                width: 75% !important;
            }

            // @include breakpoint(tablet) {
            //     width: 90% !important
            // }

            // @include breakpoint(largeTablet) {
            //     width: 90% !important
            // }
        }

        .MuiTypography-body2 {
            @include breakpoint(mobile) {
                font-size: 10px;
                width: 65% !important;
            }

            @include breakpoint(tablet) {
                width: 90% !important
            }

            @include breakpoint(largeTablet) {
                width: 90% !important
            }
        }

        .MuiImageListItem-root {
            align-items: center;
        }

        @include breakpoint(mobile) {
            width: 70%;
        }

        &_info {
            width: 100%;

            &__icon {
                // font-size: small;
            }
        }
    }

    .div-card-post-right {
        width: 100px;
        align-items: flex-end;

        @include breakpoint(mobile) {
            width: 30%;
        }

        @include breakpoint(largeTablet) {
            width: 30%;
        }
    }
}